




























































































import { Component } from 'vue-property-decorator'
import VueBase from '@/VueBase'
import { Dropdown, DropdownMenu, DropdownItem, Icon } from 'view-design'
import emitter from '../taint/Emitter'
@Component({
  name: 'layoutHeader',
  components: {
    Dropdown,
    DropdownMenu,
    DropdownItem,
    Icon,
  },
})
export default class Header extends VueBase {
  private logo_en = '/upload/assets/img/logo_en.png'
  private logo = '/upload/assets/img/logo.png'
  changelogo() {
    this.logo_en = '/upload/assets/img/logo_en.png?v=' + String(Math.random())
    this.logo = '/upload/assets/img/logo.png?v=' + String(Math.random())
  }
  private show = false
  private async changeLanguage(language: string) {
    const userInfo: any = this.userInfo
    const res = await this.services.setting.setLang(language)
    if (res.status !== 201) {
      this.$message.error(res.msg)
      return
    }
    window.localStorage.setItem(userInfo.username + '-language', language)
    switch (language) {
      case 'zh':
        this.$i18n.locale = 'zh_cn'
        break
      case 'en':
        this.$i18n.locale = 'en'
        break
      default:
        this.$i18n.locale = 'zh_cn'
        break
    }
    if (this.$router && this.$route.meta) {
      document.title = this.$t(this.$route.meta.i18n) as string
    }
    this.$emit('reload')
  }
  get userInfo(): { role: number } {
    return this.$store.getters.userInfo
  }
  async buildIAST() {
    const res = await this.services.setting.openapi()
    if (res.status === 202) {
      if (this.userInfo.role === 1 || this.userInfo.role === 2) {
        this.$message({
          type: 'warning',
          message: this.$t('base.setOpenapi') as string,
          showClose: true,
        })
        this.$router.push('/setting/serverRegister?needBack=1')
      } else {
        this.$message({
          type: 'warning',
          message: this.$t('base.helpOpenapi') as string,
          showClose: true,
        })
      }
      return
    }
    if (res.status !== 201) {
      this.$message({
        type: 'error',
        message: res.msg,
        showClose: true,
      })
      return
    }
    this.$router.push('/deploy')
  }

  canShow(name: string) {
    return this.$store.getters.routers.includes(name)
  }
  private currentRoute(path: string): boolean {
    return this.$route.matched.some((item) => item.path === path)
  }

  private async logOut() {
    await this.$store.dispatch('user/logOut')
  }
  created() {
    emitter.on('changelogo', this.changelogo)
  }
}
